html{
  min-width: 200px;
  overflow-y: scroll;
}

body {
font-family: 'DM Sans', sans-serif;
letter-spacing: 0.02em;
background-color: var(--light-theme-background);
overflow-x: hidden;
}

.list {
list-style: none;
}

.link {
font-family: inherit;
text-decoration: none;
}

p {
margin-top: 0;
margin-bottom: 0;
}

ul,
ol {
margin: 0;
padding-left: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--main-font), sans-serif;
  margin-top: 0;
  margin-bottom: 0;
}

img {
display: block;
max-width: 100%;
height: auto;
}

.is-hidden{
  display: none;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.visually-hidden{ 
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container{
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}

@media screen and (max-width: 767.8px) {
  .container {
    max-width: 375px;
    width: 100%;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439.8px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .sidebar {
    flex-direction: row;
    margin-left: 0px;
    justify-content: space-between;
}
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
    flex-direction: row;
  }
  .sidebar {
    flex-direction: column;
    row-gap: 86px;
}
}