.loader {
  width: max-content;
  height: 80px;
  --duration: 6.8s;
  --color1: var(--accent-color1);
  --color2: var(--accent-color2);
  flex-direction: column;
  justify-content: flex-end;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.loader .text {
  width: 100%;
  text-align: center;
  font-family: var(--main-font), sans-serif;
  letter-spacing: .23em;
  color: var(--color1);
  font-size: 13px;
  font-weight: 700;
  line-height: 1.2;
}

.loader .inner {
  -webkit-animation: book var(--duration) ease infinite;
  animation: book var(--duration) ease infinite;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.loader .left-top, .loader .right-top {
  width: 28px;
  height: 2px;
  background: var(--color1);
  border-radius: 2px;
  margin-bottom: 5px;
}

.loader .top {
  column-gap: 30px;
  display: flex;
}

.loader .bottom {
  display: flex;
}

.loader .left-bottom, .loader .right-bottom {
  width: 34px;
  height: 5px;
  background: var(--color2);
  border-radius: 2px;
}

.loader .left-bottom {
  margin-right: -5px;
}

.loader .right-bottom {
  margin-left: -5px;
}

.loader .middle {
  width: 33px;
  height: 12px;
  border: 5px solid var(--color2);
  border-top: 0;
  border-radius: 0 0 9px 9px;
}

.loader ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.loader ul li {
  width: 28px;
  height: 2px;
  background: var(--color1);
  transform-origin: 100% 1px;
  -webkit-animation-duration: var(--duration);
  animation-duration: var(--duration);
  border-radius: 2px;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  position: absolute;
  top: 45px;
  left: 18px;
  transform: rotateZ(0deg)translateX(-15px);
}

.loader ul li:nth-child(0) {
  animation-name: page-0;
}

.loader ul li:nth-child(1) {
  animation-name: page-1;
}

